import { NextResponse } from 'next/server';
import mongoose from 'mongoose';

const MONGODB_URI = process.env.MONGODB_URI as string;

if (!mongoose.connections[0].readyState) {
  mongoose.connect(MONGODB_URI, {
    dbName: 'rms',
  });
}

const CapAlertSchema = new mongoose.Schema(
  {
    identifier: String,
    sender: String,
    sent: Date,
    status: String,
    msgType: String,
    source: String,
    scope: String,
    restriction: String,
    addresses: [String],
    code: [String],
    note: String,
    references: String,
    incidents: String,
  },
  { collection: 'cap_alerts', timestamps: true }
);

const CapAlertModel =
  mongoose.models.CapAlert || mongoose.model('CapAlert', CapAlertSchema);

export async function GET() {
  try {
    const data = await CapAlertModel.find().limit(50).sort({ createdAt: -1 });
    return NextResponse.json({ success: true, data });
  } catch (error) {
    return NextResponse.json(
      { success: false, error: 'Failed to fetch CAP Alerts' },
      { status: 500 }
    );
  }
}
